* {
  background-color: #0f1c2e;
}

h2 {
  color: rgb(202, 206, 201);
  font-size: 2em;
  text-align: center;
  margin-top: 0;
  margin-bottom: 10px;
}
h2:hover {
  outline: none;
  color: rgb(66, 55, 55);
  transition: all 1.2s ease-in-out;
  cursor: hand;
}
h2::selection {
  outline: none;
  color: rgba(55, 78, 114, 0.425);
}
h3 {
  color: rgb(202, 206, 201);
}
.input {
  display: flex;
  align-items: center;
}
input {
  width: 100%;
  height: 2em;
  border: none;
  border-radius: 0.5em;
  background-color: rgb(202, 206, 201);
  color: rgb(2, 20, 20);
  font-size: 1em;
  padding-left: 0.5em;
  margin-right: 0.5em;
}
input:focus {
  outline: none;
  background-color: rgb(2, 20, 20);
  color: rgb(205, 206, 201);
  transition: all 1s ease-in;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 200px;
  max-width: 100%;
  object-fit: contain;
}
.card img:hover {
  cursor: pointer;
  transition: border 1s ease-in-out;
  border-style: dashed double none;
  border-radius: 10px;
}
p {
  display: none;
}
